class WorkModel {
  id;
  name;
  tags;
  date;
  sourceImage;
  link;

  constructor(data) {
    this.id = data.id;
    this.name = data.name;
    this.tags = data.tags;
    this.date = data.date;
    this.sourceImage = data.imgSrc;
    this.link = data.link;
  }

  static fromJson(data) {
    return new WorkModel(data);
  };

  static fromJsonList(data) {
    const portfolioList = [];

    data.forEach((entry) => {
      portfolioList.push(new WorkModel(entry));
    });

    return portfolioList;
  }
}

export default WorkModel;
