import { OrdinalNumber } from "constants";
import { ResponsiveHelper } from "helpers";
import styled from "styled-components";

export const WorkContainer = styled.span`
  position: absolute;
  top: 50%;
  right: 68px;
  transform: rotate(90deg) translate(-19%, -150%);

  font-size: ${({ windowSize }) => new ResponsiveHelper(windowSize).getFontSize(OrdinalNumber.PRIMARY) * 1.5}px;

  @media only Screen and (max-width: 768px) {
    right: 49px;
  }
`;
