import React from 'react';
import { BrowserRouter, Routes as RoutesContainer, Route } from 'react-router-dom';

import { About, Landing, MySkills, Portfolio, Work, PageNotFound } from 'pages';

const Routes = () => (
  <BrowserRouter>
    <RoutesContainer>
      <Route path="/portfolio" element={<Landing />} />
      <Route path="/portfolio/work" element={<Work />} />
      <Route path="/portfolio/timeline" element={<Portfolio />} />
      <Route path="/portfolio/about-me" element={<About />} />
      <Route path="/portfolio/my-skills" element={<MySkills />} />
      <Route path="/portfolio/*" element={<PageNotFound />} />
    </RoutesContainer>
  </BrowserRouter>
);

export default Routes;