import { OrdinalNumber } from "constants";
import { ResponsiveHelper } from "helpers";
import styled from "styled-components";

export const MySkillsContainer = styled.span`
position: absolute;
bottom: 50px;
right: 25%;
transform: translate(60%, 0%);

font-size: ${({ windowSize }) => new ResponsiveHelper(windowSize).getFontSize(OrdinalNumber.PRIMARY) * 1.5}px;

  @media only Screen and (max-width: 768px) {
    bottom: 30px;
  }
`;
