import React from 'react';

import { LandingActionsContainer } from 'pages/landing/sub-components/landing-actions/LandingActions.styles';
import Logo from 'components/logo/Logo';
import ContactMe from 'pages/landing/sub-components/contact-me/ContactMe';
import Work from 'pages/landing/sub-components/work/Work';
import Portfolio from 'pages/landing/sub-components/portfolio/Portfolio';
import AboutMe from 'pages/landing/sub-components/about-me/AboutMe';
import MySkills from 'pages/landing/sub-components/my-skills/MySkills';

const LandingActions = ({ windowSize }) => {
  return <LandingActionsContainer>
    <Logo windowSize={windowSize} />
    <ContactMe windowSize={windowSize} />
    <Work windowSize={windowSize} />
    <Portfolio windowSize={windowSize} />
    <AboutMe windowSize={windowSize} />
    <MySkills windowSize={windowSize} />
  </LandingActionsContainer>;
};

export default LandingActions;
