import React from 'react';

import { MySkillsContainer } from 'pages/landing/sub-components/my-skills/MySkills.styles';
import { NavLink } from 'react-router-dom';

const MySkills = ({ windowSize }) => {
  return <NavLink to="/portfolio/my-skills" >
    <MySkillsContainer windowSize={windowSize} >
      My skills
    </MySkillsContainer>
  </NavLink>;
};

export default MySkills;
