import { OrdinalNumber } from "constants";
import { ResponsiveHelper } from "helpers";
import styled from "styled-components";

export const LandingContainer = styled.div`
  display: flex;
  background-color: #FCF6F4;
  width: 100%;
  height: 100vh;

  justify-content: center;
  align-items: center;

  overflow: hidden;
`;

export const LandingContentContainer = styled.div`
  display: flex;
  height: 60%;
  width: 65%;
  border: 3px solid #000000;

  @media only Screen and (max-width: 768px) {
    flex-direction: column;
    height: 40%;
  }
`;

export const LandingContentGreetingsContainer = styled.div`
  flex: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;

  padding: 50px;

  @media only Screen and (max-width: 768px) {
    padding: 20px;
    flex: 1;
  }
`;

export const LandingContentGreetingsText = styled.span`
  font-weight: ${({ fontWeight }) => fontWeight};

  padding: 5px 0px;

  @media only Screen and (max-width: 768px) {
    text-align: center;
  }
`;

export const LandingGreetingsPrimary = styled(LandingContentGreetingsText)`
  font-size: ${({ windowSize }) => new ResponsiveHelper(windowSize).getFontSize(OrdinalNumber.PRIMARY) * 2}px;
`;

export const LandingGreetingsSecondary = styled(LandingContentGreetingsText)`
  font-size: ${({ windowSize }) => new ResponsiveHelper(windowSize).getFontSize(OrdinalNumber.SECONDARY) * 1.5}px;
`;


export const LandingContentCharacterContainer = styled.div`
  flex: 3;
  display: flex;
  justify-content: center;
  align-items: center;

  @media only Screen and (max-width: 768px) {
    flex: 1;
  }
`;
