import { OrdinalNumber } from "constants";
import { ResponsiveHelper } from "helpers";
import styled from "styled-components";

export const PortfolioContainer = styled.span`
  position: absolute;
  top: 50%;
  left: 21px;
  transform: rotate(270deg) translate(14%, -50%);

  font-size: ${({ windowSize }) => new ResponsiveHelper(windowSize).getFontSize(OrdinalNumber.PRIMARY) * 1.5}px;

  @media only Screen and (max-width: 768px) {
    left: 7px;
  }
`;
