import { OrdinalNumber } from "constants";
import { ResponsiveHelper } from "helpers";
import styled from "styled-components";

export const LogoContainer = styled.span`
  position: absolute;
  top: 40px;
  left: 50px;
  background-color: #fcf6f4;
  padding: 10px;
  margin: -10px;

  font-size: ${({ windowSize }) => new ResponsiveHelper(windowSize).getFontSize(OrdinalNumber.PRIMARY) * 1.8}px;

  @media only Screen and (max-width: 768px) {
    top: 20px;
    left: 30px;
  }
`;