import React from 'react';
import { DefaultParticle } from 'components';
import Logo from 'components/logo/Logo';
import { WorkContainer, WorkGrid } from 'pages/work/Work.styles';
import { WorkItemCard } from 'pages/work/sub-components';

const WorkComponent = ({ windowSize, workList }) => (
  <>
    <DefaultParticle />
    <Logo windowSize={windowSize} />

    <WorkContainer>
      <WorkGrid>
        {workList.map((data, key) => <WorkItemCard
          key={key}
          data={data}
          windowSize={windowSize} />)}
      </WorkGrid>
    </WorkContainer>
  </>
);

export default WorkComponent;
