import { OrdinalNumber } from "constants";
import { ResponsiveHelper } from "helpers";
import styled from "styled-components";

export const AboutMeContainer = styled.span`
  position: absolute;
  bottom: 50px;
  left: 25%;
  transform: translate(-55%, 0%);

  font-size: ${({ windowSize }) => new ResponsiveHelper(windowSize).getFontSize(OrdinalNumber.PRIMARY) * 1.5}px;

  @media only Screen and (max-width: 768px) {
    bottom: 30px;
  }
`;
