import { OrdinalNumber } from "constants";
import { ResponsiveHelper } from "helpers";
import styled from "styled-components";

export const ContactMeContainer = styled.a`
  position: absolute;
  top: 40px;
  right: 50px;

  font-size: ${({ windowSize }) => new ResponsiveHelper(windowSize).getFontSize(OrdinalNumber.PRIMARY) * 1.5}px;

  @media only Screen and (max-width: 768px) {
    top: 20px;
    right: 30px;
  }
`;
