import { OrdinalNumber } from "constants";

class ResponsiveHelper {
  constructor(windowResize) {
    this.width = windowResize[0];
    this.height = windowResize[1];
  }

  getWidth({ small, medium, large, xLarge, xxLarge }) {
    const width = this.width;

    const maxWidth = xxLarge ?? xLarge ?? large ?? medium ?? small ?? 0;

    if (width > 1400) {
      return maxWidth;
    } else if (width > 1200) {
      return xLarge ?? maxWidth;
    } else if (width > 992) {
      return large ?? xLarge ?? maxWidth;
    } else if (width > 768) {
      return medium ?? large ?? xLarge ?? maxWidth;
    } else {
      return small ?? medium ?? large ?? xLarge ?? maxWidth;
    }
  }

  getFontSize(theme) {
    if (this.width > 768) {
      switch (theme) {
        case OrdinalNumber.SECONDARY:
          return 16;
        default:
          return 18;
      }
    } else {
      switch (theme) {
        case OrdinalNumber.SECONDARY:
          return 12;
        default:
          return 14;
      }
    }
  }
}

export default ResponsiveHelper;
