import React from 'react';

import {
  LandingContainer,
  LandingContentCharacterContainer,
  LandingContentContainer,
  LandingContentGreetingsContainer,
  LandingGreetingsPrimary,
  LandingGreetingsSecondary
} from 'pages/landing/Landing.styles';
import { LandingActions } from './sub-components';
import { MeInLaptop } from 'components';
import { ResponsiveHelper } from 'helpers';

const LandingComponent = ({ windowSize }) => (
  <>
    <LandingContainer>
      <LandingContentContainer>
        <LandingContentGreetingsContainer>
          <LandingGreetingsPrimary fontWeight={500} windowSize={windowSize}>Hi,</LandingGreetingsPrimary>
          <LandingGreetingsPrimary fontWeight={600} windowSize={windowSize}>I'm Shan,</LandingGreetingsPrimary>
          <LandingGreetingsSecondary windowSize={windowSize}>I can turn your ideas into great website solutions for your businesses</LandingGreetingsSecondary>
        </LandingContentGreetingsContainer>

        <LandingContentCharacterContainer>
          <MeInLaptop size={new ResponsiveHelper(windowSize).getWidth({ large: 400, medium: 300, small: 200 })} />
        </LandingContentCharacterContainer>
      </LandingContentContainer>
    </LandingContainer>

    <LandingActions windowSize={windowSize} />
  </>
);

export default LandingComponent;
