import React from 'react';

import {
  WorkCardContainer,
  WorkItemCardDate,
  WorkItemCardImage,
  WorkItemCardTags,
  WorkItemCardTagsContainer,
  WorkItemCardTitle
} from 'pages/work/sub-components/work-item-card/WorkItemCard.styles';

const WorkItemCard = ({ data, windowSize }) => (
  <WorkCardContainer>
    <WorkItemCardImage image={data.sourceImage} />
    <WorkItemCardTitle windowSize={windowSize}>{data.name}</WorkItemCardTitle>

    <WorkItemCardTagsContainer>
      {data.tags.map((tag, key) => <WorkItemCardTags windowSize={windowSize} key={key}>
        {`#${tag}`}
      </WorkItemCardTags>)}
    </WorkItemCardTagsContainer>

    <WorkItemCardDate windowSize={windowSize}>{data.date}</WorkItemCardDate>
  </WorkCardContainer>
);

export default WorkItemCard;
