import { OrdinalNumber } from 'constants';
import { ResponsiveHelper } from 'helpers';
import styled from 'styled-components';

export const MySkillsContainer = styled.div`
  display: grid;
  grid-template-columns: repeat(2, calc(50% - 20px));
  grid-gap: 20px 40px;
  padding: 100px 100px 30px 100px;
  justify-items: center;

  @media only Screen and (max-width: 1075px) {
    grid-template-columns: 100%;
  }

  @media only Screen and (max-width: 770px) {
    padding: 70px 100px 30px 100px;
  }

    @media only Screen and (max-width: 530px) {
      padding: 70px 30px 30px 30px;
    }
`;

export const MySkillsItemContainer = styled.div`
  width: 100%;

  border: 2px solid ${props => props.theme.text};
  backdrop-filter: blur(2px);

  &:hover {
    color: #fff;
    background-color: #000000;
    transition: all 0.3s ease;
  }

  &>div {
    padding: 50px 40px;
  }
`;

export const MySkillsItemTitleContainer = styled.div`
  display: flex;
  margin-top: -10px;
  padding-bottom: 30px;

  justify-content: center;
  align-items: center;

  &>svg {
    margin-right: 15px;
  }

  ${MySkillsItemContainer}:hover &>svg {
      fill: #fff;
      transition: all 0.3s ease;
    }
`;

export const MySkillsItemContainerTitle = styled.span`
  font-weight: bold;
  font-size: ${({ windowSize }) => new ResponsiveHelper(windowSize).getFontSize(OrdinalNumber.PRIMARY) * 1.5}px;
`;

export const MySkillsItemContentContainer = styled.div`
  padding: 0 0 20px 0;

  ul {
    margin-left: 32px;
    padding-top: 10px;
  }

  li {
    padding: 5px 0;
    font-size: ${({ windowSize }) => new ResponsiveHelper(windowSize).getFontSize(OrdinalNumber.SECONDARY)}px;
  }

  &:last-child {
    padding: 0;
  }
`;

export const MySkillsItemContentText = styled.span`
  display: block;
  font-size: ${({ windowSize }) => new ResponsiveHelper(windowSize).getFontSize(OrdinalNumber.SECONDARY)}px;
`;

export const MySkillsItemTitle = styled.span`
  font-weight: bold;
  font-size: ${({ windowSize }) => new ResponsiveHelper(windowSize).getFontSize(OrdinalNumber.PRIMARY)}px;
`;
