import React from 'react';
import { NavLink } from 'react-router-dom';
import { PortfolioContainer } from 'pages/landing/sub-components/portfolio/Portfolio.styles';

const Portfolio = ({ windowSize }) => {
  return <NavLink to="/portfolio/timeline">
    <PortfolioContainer windowSize={windowSize} >
      Timeline
    </PortfolioContainer>
  </NavLink>;
};

export default Portfolio;
