import { defaultParticleConfig } from "assets/particles";
import Particles from "react-tsparticles";
import { loadFull } from "tsparticles";

const DefaultParticle = () => {
  const particlesInit = async (main) => {
    await loadFull(main);
  };

  return <Particles
    id="tsparticles"
    init={particlesInit}
    options={defaultParticleConfig} />;
};

export default DefaultParticle;

