import { OrdinalNumber } from "constants";
import { ResponsiveHelper } from "helpers";
import styled from "styled-components";

export const WorkCardContainer = styled.a`
  width: calc(100% - 70px);
  height: 230px;
  padding: 20px;

  border: 2px solid ${props => props.theme.text};
  backdrop-filter: blur(2px);

  cursor: pointer;

  &:hover {
    color: #fff;
    background-color: #000000;
    transition: all 0.3s ease;
  }

  @media only Screen and (max-width: 768px) {
    width: 100%;
    height: 210px;
  }
`;

export const WorkItemCardImage = styled.div`
  background-color: #fff;
  background-image: ${({ image }) => `url(${window.location.origin}${image})`};
  width: 100%;
  height: 100px;
  background-size: contain;
  background-repeat: no-repeat;
  border: 1px solid transparent;
  background-position: center center;

  @media only Screen and (max-width: 768px) {
    height: 92px;
  }
`;

export const WorkItemCardTitle = styled.span`
  width: calc(100% - 16px);
  border-bottom: 1px solid #000;
  padding: 16px 0 8px 0;
  display: inline-block;

  font-weight: 500;
  font-size: ${({ windowSize }) => new ResponsiveHelper(windowSize).getFontSize(OrdinalNumber.PRIMARY)}px;
`;

export const WorkItemCardTagsContainer = styled.div`
  height: 30px;
  padding: 8px 0 0 0;

  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;

  ::-webkit-scrollbar {
    display: none;
  }

  &>*:first-child {
    padding: 0 8px 0 0;
  }

  @media only Screen and (max-width: 768px) {
    height: 20px;
  }
`;

export const WorkItemCardTags = styled.span`
  padding: 8px;

  font-size: ${({ windowSize }) => new ResponsiveHelper(windowSize).getFontSize(OrdinalNumber.SECONDARY)}px;
`;

export const WorkItemCardDate = styled.span`
  position: absolute;
  bottom: 20px;

  font-size: ${({ windowSize }) => new ResponsiveHelper(windowSize).getFontSize(OrdinalNumber.SECONDARY)}px;
`;
