import React from 'react';
import { NavLink } from 'react-router-dom';
import { AboutMeContainer } from './AboutMe.styles';

const AboutMe = ({ windowSize }) => {
  return <NavLink to="/portfolio/about-me">
    <AboutMeContainer windowSize={windowSize} >
      About me
    </AboutMeContainer>
  </NavLink>;
};

export default AboutMe;
